ul.timeline {
    position: relative;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  
  .timeline:before {
    position: absolute;
    left: 50%;
    top: 0;
    content: ' ';
    display: block;
    width: 2px;
    height: 100%;
    margin-left: -1px;
    background: rgb(213,213,213);
    background: -moz-linear-gradient(top, rgba(213,213,213,0) 0%, rgb(213,213,213) 8%, rgb(213,213,213) 92%, rgba(213,213,213,0) 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(30,87,153,1)), color-stop(100%,rgba(125,185,232,1)));
    background: -webkit-linear-gradient(top, rgba(213,213,213,0) 0%, rgb(213,213,213) 8%, rgb(213,213,213) 92%, rgba(213,213,213,0) 100%);
    background: -o-linear-gradient(top, rgba(213,213,213,0) 0%, rgb(213,213,213) 8%, rgb(213,213,213) 92%, rgba(213,213,213,0) 100%);
    background: -ms-linear-gradient(top, rgba(213,213,213,0) 0%, rgb(213,213,213) 8%, rgb(213,213,213) 92%, rgba(213,213,213,0) 100%);
    background: linear-gradient(to bottom, rgba(213,213,213,0) 0%, rgb(213,213,213) 8%, rgb(213,213,213) 92%, rgba(213,213,213,0) 100%);
    z-index: 5;
  }
  
  .timeline li {
    padding: 1px 0 2em 0;
  }
  
  .timeline svg {
    width: 20px;
    height: 20px;
    position: absolute;
    background: transparent;
    z-index: 5;
    left: 0;
    right: 0;
    top: 19px;
    margin-top: 0;
  }
  .timeline svg circle {
    fill: #00c4f3;
  }
  
  .dr {
    width: 50%;margin-left: 50%;position: relative;
  }
  .dl {
    width: 50%;margin-left: 0%;position: relative;
  }
  
  .dr svg {
      left: -10px;
      right: auto;
  }
  .dl svg {
      left: auto;
      right: -10px;
  }
  
  .time-wrapper {
     position: absolute;
     top: 17px;
     width: 100%;
  }
  .dr .time-wrapper {
    left: -100%;
    text-align: right;
     padding-right: 15px;
  }
  .dl .time-wrapper {
    right: -100%;
    text-align: left;
    padding-left: 15px;
  }
  .time {
     display: inline;
     padding: 4px 8px;
     background-color: #00c4f3;
     color: #fff;
     font-size: 14px;
  }
  
  .flag-wrapper {
    position: relative;
    top: 5px; 
  }
  .dr .flag-wrapper {
    margin-left: 28px;
  }
  .dl .flag-wrapper {
    margin-right: 28px;
  }
  .flag-wrapper p {
    margin-top: -9px;
  }
  
  .flag {
    position: relative;
    display: inline;
    background-color: #fefefe;
    z-index: 15;
    padding: 6px 10px;
    text-align: left;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
  }
  
  .dr .flag {
    float: left;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    -webkit-box-shadow: 1px 1px 1px rgba(0,0,0,0), 0 0 1px rgba(0,0,0,0);
    -moz-box-shadow: 1px 1px 1px rgba(0,0,0,0), 0 0 1px rgba(0,0,0,0);
    box-shadow: 1px 1px 1px rgba(0,0,0,0), 0 0 1px rgba(0,0,0,0);
  }
  .dl .flag {
    float: right;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    -webkit-box-shadow: -1px 1px 1px rgba(0,0,0,0), 0 0 1px rgba(0,0,0,0);
    -moz-box-shadow: -1px 1px 1px rgba(0,0,0,0), 0 0 1px rgba(0,0,0,0);
    box-shadow: -1px 1px 1px rgba(0,0,0,0), 0 0 1px rgba(0,0,0,0);
  }
  
  .arrow-box {
    position: relative;
    background: transparent;
  }
  .dr .arrow-box:after {
    left: -28px;
    top: 0px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(254, 254, 254, 0);
    border-right-color: #fefefe;
    border-width: 15px;
    margin-top: 1px;
  }
  .dl .arrow-box:after {
    right: -28px;
    top: 0px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(254, 254, 254, 0);
    border-left-color: #fefefe;
    border-width: 15px;
    margin-top: 1px;
  }
  
  .desc {
    clear: both;
    position: relative;
    padding: 10px 15px 15px;
    background-color: #fefefe;
    -webkit-box-shadow: 0 0 1px rgba(0,0,0,0.20);
    -moz-box-shadow: 0 0 1px rgba(0,0,0,0.20);
    box-shadow: 0 0 1px rgba(0,0,0,0.20);
    z-index: 15;
    font-size: 16px;
    color: #666;
    line-height: 20px;
  }
  .desc .jobTitle {
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .dr .desc {
    margin: .8em 0 0 2em;
    text-align: left;
  }
  .dl .desc {
    margin: .8em 2em 0 0;
    text-align: right;
  }
  
  
  @media (max-width: 576px) {
    .timeline li {
      padding: 2em 0 2em;
    }
    .timeline svg {
      display: none;
    }
    .dr, .dl {
      width: 100%; margin-left: 0;
    }
    .dr .time-wrapper {
      top: 12px;
      left: 0;
      padding-right: 0;
      text-align: left;
    }
    .dl .time-wrapper {
      top: 12px;
      right: 0;
      text-align: right;
    }
    .dr .flag-wrapper {
      margin-top: 0;
      margin-left: 0;
    }
    .dl .flag-wrapper {
      margin-top: 0;
      margin-right: 0;
    }
    .dr .flag,
    .dl .flag {
      border-radius: 0;
    }
    .dr .desc,
    .dl .desc {
      margin: .5em 0 0 0;
    }
  }
  
